import { Divider } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Fn } from "../../../../helper/function";
import { PaginationRequest, TableColumn } from "../../../../models/baseModel";
import { TaskProjectManageService } from "../../../../services/taskService";
import { Box } from "../../../core/MyBox";
import { OrderByModel, PageInfoModel } from "../../../core/TableModel";
import TableComponent from "../../../core/TableNewComponent";
import { UIFormatDateTimeVNComponent } from "../../../core/UI_FormatComponent";

export default function LogProjectComponent(props: any) {
    const column: any = [
        new TableColumn({
            title: "STT", key: "stt", render: (data: any, row: any, index: any) =>
                Fn.RenderSTT(data, row, index, pageInfo)
        }),
        new TableColumn({
            title: "Loại", key: "type", render: (data: number) => {
                let type = "";
                switch (data) {
                    case 1:
                        type = "Dự án";
                        break;

                    case 2:
                        type = "Sprint";
                        break;

                    case 3:
                        type = "Cài đặt";
                        break;

                    default:
                        break;
                }
                return (
                    <span>
                        {type}
                    </span>
                );
            }
        }),
        new TableColumn({ title: "Hành động", key: "action" }),
        new TableColumn({
            title: "Người dùng", key: "user", render: (data: any) =>
                <span>{data.name}</span>
        }),
        new TableColumn({
            title: "Thời gian", key: "createdAt", render: (data: any) =>
                <UIFormatDateTimeVNComponent value={data} />
        })
    ];
    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const [data, setData] = useState([]);
    const { isLoading, setIsLoading, projectKey } = props;

    const loadData = useCallback(() => {
        setIsLoading(true);
        let paginationRequest: PaginationRequest = new PaginationRequest(pageInfo.pageNumber, pageInfo.pageSize, orderBy);
        TaskProjectManageService.GetLog(projectKey, paginationRequest)
            .then((res: any) => {
                if (res.isSuccess) {
                    setPageInfo(new PageInfoModel(res));
                    setData(res.data.pagedData);
                }
            })
            .finally(() => { setIsLoading(false); })
    }, [projectKey, setIsLoading, pageInfo.pageNumber, pageInfo.pageSize, orderBy]);

    useEffect(() => {
        loadData();
    }, [loadData])

    return (
        <Box showBox={false}>
            <Divider orientation="left" style={{ borderTopColor: "#000" }}>
                <h4 style={{ fontWeight: "bold" }}>Nhật ký hoạt động</h4>
            </Divider>
            <TableComponent
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                orderBy={orderBy}
                setOrderBy={setOrderBy}
                column={column}
                dataSource={data}
                loading={isLoading}
                loadData={loadData} />
        </Box>
    )
}